* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

::selection {
    background-color: #fbc108;
    color: #fff;
}

::-webkit-scrollbar {
    width: 8px;
    background-color: #9661ff;
}

::-webkit-scrollbar-thumb {
    width: 100%;
    background-color: #fbc108;
}

header {
    padding: 10px 90px;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    background-color: #fff;
    z-index: 9;
}

header nav {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

header nav .logo img {
    height: 75px;
}

header nav .nav-links {
    margin: auto;
}

header nav .nav-links a,
header nav .drop-down {
    display: inline-block;
    text-decoration: none;
    color: #4a6f8a;
    font-weight: 600;
    font-size: 15px;
    margin: 0 15px;
    cursor: pointer;
}

header nav .nav-links a:hover,
header nav .nav-links a:focus,
header nav .drop-down:hover,
header nav .drop-down:focus {
    color: #fbc108;
}

header nav .call a,
.btn {
    text-decoration: none;
    transition: all 0.5s;
    color: #fff;
    background-color: #9765f9;
    padding: 17px 25px;
    display: inline-block;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
    border: none;
    outline: none;
}

header nav .call a:hover,
header nav .call a:focus,
.btn:hover,
.btn:focus {
    background-color: #fbc20b;
    box-shadow: 0 0 10px 0px rgb(0 0 0 / 50%);
}

.toglar-button {
    display: none;
}

.container {
    padding: 0 90px;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.justify-content-center {
    justify-content: center;
}

.col-md-7 {
    flex: 0 0 58.33333%;
    padding: 0 10px;
}

.col-md-5 {
    flex: 0 0 41.6666%;
    padding: 0 16px;
}

.col-md-6 {
    flex: 0 0 50%;
    padding: 0 16px;
}

.col-md-3 {
    flex: 0 0 33.3333%;
    padding: 0 16px;
}

.col-md-4 {
    flex: 0 0 25%;
    padding: 0 16px;
}

.w-100 {
    width: 100%;
}

.banner h3 {
    margin: 30px 0 20px 0;
    line-height: 55px;
    font-size: 45px;
    font-weight: 700;
}

.banner p {
    font-size: 18px;
    color: #4a6f8a;
    margin-bottom: 30px;
    line-height: 27px;
}

.banner {
    padding: 50px 0 30px 0;
    background: url(./images/home-shape.png) bottom no-repeat;
    z-index: 9999;
}

.how {
    text-align: center;
    padding: 20px 10px;
    border-radius: 10px;
    background-color: #f3f3f3;
    height: 100%;
}

.how i {
    display: block;
    margin: 10px auto;
    font-size: 25px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: #4a6f8a;
    color: #fff;
    line-height: 70px;
}

.how a {
    display: inline-block;
    text-decoration: none;
    font-size: 17px;
    color: #000;
    font-weight: 600;
    line-height: 1.4;
}

.how a:hover,
.how a:focus {
    color: #fbc108;
}

.how p {
    margin-top: 5px;
    margin-bottom: 16px;
    font-size: 15px;
    line-height: 1.4;
}

.how:hover {
    box-shadow: 0 0 2px 0 rgb(0 0 0 / 50%);
}

section {
    padding: 70px 0;
}

section:nth-child(even) {
    background-color: #f3f3f3;
}

section:nth-child(odd) {
    background-color: #fff;
}

.about h3 {
    font-size: 32px;
    margin-bottom: 16px;
}

.about h5 {
    font-size: 1.25rem;
    margin-bottom: 12px;
}

.about p {
    font-size: 17px;
    margin-bottom: 1rem;
    line-height: 23px;
    color: #4a6f8a;
    text-align: justify;
}

.service {
    padding: 20px 30px;
    border-radius: 7px;
    background-color: #f3f3f3;
    height: 100%;
    position: relative;
    top: 0;
    transition: all 0.5s;
}

.service i {
    display: block;
    text-align: center;
    margin: 10px 0;
    font-size: 25px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: #4a6f8a;
    color: #fff;
    line-height: 70px;
}

.service a {
    display: inline-block;
    text-decoration: none;
    font-size: 17px;
    color: #000;
    font-weight: 600;
    line-height: 1.4;
    margin-top: 5px;
}

.service a:hover,
.service a:focus {
    color: #fbc108;
}

.service p {
    margin-top: 5px;
    margin-bottom: 16px;
    font-size: 15px;
    line-height: 1.4;
}

.service:hover {
    box-shadow: rgb(255 255 255 / 10%) 0px 1px 1px 0px inset, rgb(50 50 93 / 25%) 0px 50px 100px -20px, rgb(0 0 0 / 30%) 0px 30px 60px -30px;
    top: -5px;
}

.service .link {
    display: flex;
    color: #4a6f8a;
    align-items: center;
    font-size: 15px;
}

.service .link:hover i,
.service .link:focus i {
    color: #fbc108;
}

.service .link i {
    background-color: transparent;
    display: inline-block;
    color: #4a6f8a;
    line-height: normal;
    height: auto;
    width: auto;
    font-size: 16px;
    margin-left: 5px;
}

.price {
    padding: 37px 35px 36px 50px;
    border-radius: 7px;
    background: #fff;
    position: relative;
    top: 0;
    transition: all 0.5s;
}

.price:hover {
    box-shadow: rgb(255 255 255 / 10%) 0px 1px 1px 0px inset, rgb(50 50 93 / 25%) 0px 50px 100px -20px, rgb(0 0 0 / 30%) 0px 30px 60px -30px;
    top: -5px;
}

.price .price-heading {
    font-size: 30px;
    margin-bottom: 15px;
}

.price ul {
    list-style: none;
    margin-left: 10px;
}

.price ul li {
    list-style: none;
    padding: 5px 0 7px 7px;
}

.price ul li i {
    margin-right: 5px;
}

.price a {
    display: inline-block;
    margin-top: 20px;
    text-decoration: none;
    color: #000;
    font-weight: 600;
    font-size: 15px;
}

.price a:hover,
.price a:focus {
    color: #fbc108;
}

.review {
    padding: 20px 30px;
    border-radius: 10px;
    background-color: #f3f3f3;
}

.review h3 {
    margin-bottom: 10px;
}

.review p {
    color: #4a6f8a;
    text-align: justify;
    font-size: 15px;
    line-height: 21px;
}

footer {
    background: url(./images/footer.jpg) center center no-repeat;
    background-size: cover;
    padding: 70px 0 30px 0;
}

footer .logo-div p {
    padding: 0 10px;
    text-align: justify;
}

footer .row {
    margin-bottom: 35px;
}

footer .footer-links h2 {
    margin-bottom: 25px;
    margin-top: 15px;
}

footer .footer-links li {
    padding: 0 0 0 7px;
    margin: 7px 0;
}

footer .footer-links li a {
    text-decoration: none;
    color: #4a6f8a;
    font-weight: 600;
}

footer .footer-links li a:hover,
footer .footer-links li a:focus {
    color: #fbc108;
}

.footer-contact h2 {
    margin-bottom: 25px;
    margin-top: 15px;
}

.footer-contact li {
    list-style: none;
    margin: 7px 0;
}

.footer-contact li a {
    text-decoration: none;
    color: #4a6f8a;
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;
}

.footer-contact li a i {
    margin-right: 5px;
}

footer .copy-right {
    text-align: center;
    margin-top: 25px;
    font-weight: 600;
    color: #4a6f8a;
}

.service-page h2 {
    margin-bottom: 15px;
    font-size: 30px;
}

.service-page p {
    color: #4a6f8a;
    font-weight: 500;
    text-align: justify;
    line-height: 26px;
    margin-bottom: 25px;
}

header nav .drop-down {
    position: relative;
}

header nav .drop-down ul {
    display: none;
    list-style: none;
    position: absolute;
    width: 260px;
    padding: 10px 0;
    background: #f3f3f3;
    border-radius: 5px;
    margin-top: 10px;
    box-shadow: 0 0 10px rgb(0 0 0 / 30%);
}

header nav .drop-down ul li {
    padding: 10px 0;
}

header nav .drop-down.active .open-down {
    display: block;
}

.contact-page .bg-contact {
    background-color: #f3f3f3;
    padding: 30px 40px;
    border-radius: 10px;
}

.contact-page h3 {
    font-size: 25px;
    margin-bottom: 15px;
}

.contact-page li {
    font-size: 14px;
    margin: 5px 0;
}

.contact-page .contact-details h2 {
    font-size: 17px;
    margin: 20px 0 10px 0;
}

.contact-page .contact-details li {
    list-style: none;
    margin: 10px 0;
}

.contact-page .contact-details li a {
    text-decoration: none;
    color: #4a6f8a;
}

.contact-page .contact-details li a i {
    margin-right: 10px;
}

.contact-page form {
    padding: 0 30px;
}

.contact-page form h4 {
    font-size: 25px;
    margin-top: 20px;
}

.contact-page form input,
.contact-page form textarea {
    width: 100%;
    outline: none;
    border: 1px solid #4a6f8a;
    margin-top: 10px;
    padding: 9px 14px;
    font-size: 14px;
    border-radius: 5px;
}

.contact-page form .btn {
    padding: 14px 25px;
    margin-top: 15px;
}

.title-section {
    background: linear-gradient(90deg, #9661ff, #9765f9, #fbc108, #fbc20b);
    padding: 30px 100px;
    color: #fff;
    text-transform: uppercase;
}

.section-heading {
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 45px;
}

.section-heading h2 {
    font-size: 30px;
    font-family: cursive;
}

.profile h4 {
    font-size: 30px;
    margin-bottom: 25px;
}

.profile p {
    line-height: 26px;
    text-align: justify;
}

.profile .btn {
    margin-top: 15px;
}

.d-block {
    display: block !important;
}

header nav.tog {
    display: block;
}

.respo-nav {
    display: flex;
    margin-left: auto;
}

/* Responsive */

@media screen and (max-width:1250px) {
    .container {
        padding: 0 60px;
    }
}

@media screen and (max-width:1150px) {
    .container {
        padding: 0 50px;
    }
    section {
        padding: 60px 0;
    }

    .price {
        padding: 37px 30px 25px 23px;
    }
}

@media screen and (max-width:1100px) {
    header {
        padding: 10px 60px;
    }

    .col-md-4 {
        flex: 0 0 50%;
        margin-top: 15px;
    }

    footer {
        padding: 50px 0 30px 0;
    }
}

@media screen and (max-width:991px) {
    .col-md-6 {
        flex: 0 0 100%;
        margin-top: 15px;
    }

    .container {
        padding: 0 30px;
    }

    .price ul {
        padding: 0;
    }

    .toglar-button {
        display: block;
        margin-left: auto;
    }

    header nav .respo-nav {
        display: none;
        flex: 0 0 100%;
        margin: 0;
    }

    header nav .nav-links a,
    header nav .drop-down {
        display: block;
        margin: 10px 0;
    }

    header nav .drop-down ul li {
        padding: 5px 0 0 20px;
    }

    header {
        background-color: #fff;
    }

    .toglar-button i {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        text-align: center;
        line-height: 50px;
        box-shadow: 0 0 10px rgb(0 0 0 / 20%);
    }

    .toglar-button i:hover {
        box-shadow: 0 0 10px rgb(0 0 0 / 50%);
    }
}

@media screen and (max-width:800px) {
    header {
        padding: 0 50px;
    }
    .col-md-3 {
        flex: 0 0 50%;
        margin-top: 15px;
    }
}

@media screen and (max-width:620px) {
    .col-md-3 {
        flex: 0 0 100%;
    }

    header {
        padding: 10px 40px;
    }

    .title-section {
        padding: 20px 30px;
    }

    .contact-page form {
        padding: 0 10px;
        margin-bottom: 20px;
    }

    .contact-page .bg-contact {
        padding: 20px 0 20px 20px;
    }
}

@media screen and (max-width:550px) {
    header {
        padding: 10px 30px;
    }

    .col-md-4 {
        flex: 0 0 100%;
        margin-top: 15px;
    }

    section {
        margin-top: 15px;
    }

    section {
        padding: 20px 10px;
    }

    .banner h3 {
        margin: 0;
        line-height: 35px;
        font-size: 27px;
    }

    .banner p {
        font-size: 15px;
        margin-bottom: 20px;
        line-height: 23px;
    }

    .container {
        padding: 0;
    }

    header nav .logo img {
        height: 45px;
    }

    .section-heading {
        margin-bottom: 10px;
    }
}

@media screen and (max-width:450px) {
    header {
        padding: 10px 20px;
    }
    
    .contact-page form {
        padding: 0;
    }

    footer .logo-div p {
        padding: 0;
    }
}